
html {
  box-sizing: border-box;
}
*,
*:after,
*:before {
  box-sizing: inherit;
}
body {
  background: #222;
  margin: 0;
  padding: 0;
}
iframe {
  overflow: hidden;
}
.hung {
  padding: 30px 0;
}
.hung p {
    text-align: center !important;
}
.hung img {
    width: 145px;
}
#logo {
  background: rgba(255, 255, 255, 0.6);
  padding: 10px;
}
@media (min-width: 320px) and (max-width: 736px) {
#logo {
      background: transparent;
      padding: 0;
}
}
#logo svg {
    display: block;
}
header {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  padding: 30px 0;
  z-index: 999;
}
@media (min-width: 320px) and (max-width: 736px) {
header {
      padding: 15px 0;
      background: #222;
      border-bottom: 1px solid rgba(255, 255, 255, 0.1);
}
}
header svg {
    fill: #222;
    width: 70px;
}
@media (min-width: 320px) and (max-width: 736px) {
header svg {
        width: 20px;
        fill: #fff;
}
}
header nav {
    display: flex;
    align-items: flex-start;
    justify-content: space-between;
}
@media (min-width: 320px) and (max-width: 736px) {
header nav {
        flex-direction: column;
        align-items: center;
}
}
.wrapper {
  max-width: 1200px;
  width: 100%;
  padding: 0 20px;
  margin: 0 auto;
}
.process {
  list-style: none;
  margin: 0;
  padding: 0;
  display: grid;
  grid-template-columns: 1fr 1fr 1fr;
}
@media (min-width: 320px) and (max-width: 736px) {
.process {
      grid-template-columns: 1fr;
}
}
.process li {
    text-align: center;
    flex: 1;
    display: flex;
    align-items: center;
    justify-content: center;
    flex-direction: column;
    margin: 30px 30px 60px;
}
.process li h2 {
      margin: 10px;
}
.process li p {
      margin: 0;
}
.process .img-wrap {
    width: 145px;
    height: 145px;
    overflow: hidden;
    position: relative;
}
.process .img-wrap img {
      height: 100%;
      position: absolute;
      top: 50%;
      left: 50%;
      transform: translate(-50%, -50%);
}
.container {
  display: flex;
  justify-content: center;
}
@media (min-width: 320px) and (max-width: 736px) {
.container {
      flex-direction: column;
}
}
.container .col {
    max-width: 400px;
    width: 100%;
    padding: 0 20px;
    text-align: left;
}
.container ul {
    font-weight: 300;
    font-size: 18px;
    line-height: 1.5;
}
.container h1 {
    font-weight: 500;
    font-size: 35px;
    text-transform: uppercase;
}
.container h2 {
    font-weight: 500;
    font-size: 18px;
}
.container p {
    font-weight: 300;
}
.container p strong {
      font-weight: 500;
}
.navigation {
  list-style: none;
  display: flex;
  align-items: center;
  margin: 0;
  padding: 0;
}
@media (min-width: 320px) and (max-width: 736px) {
.navigation {
      padding: 0;
}
}
.navigation li {
    text-transform: uppercase;
    margin: 0 30px 0 0;
    font-weight: 500;
    font-size: 14px;
    cursor: pointer;
    color: #777;
}
.navigation li:last-of-type {
      margin: 0;
}
@media (min-width: 320px) and (max-width: 736px) {
.navigation li {
        margin: 10px 10px 0 0;
        font-size: 12px;
}
.navigation li:last-of-type {
          margin: 10px 0 0;
}
}
.navigation li a {
      text-decoration: none;
      color: #777;
      transition: all 0.4s;
}
.navigation li a:hover {
        color: #999;
}
.welcome {
  text-align: left;
}
@media (min-width: 320px) and (max-width: 736px) {
.welcome {
      text-align: center;
}
}
.welcome h1 {
    font-weight: 500;
    font-size: 50px;
    text-transform: uppercase;
    margin: 0;
    color: #222;
}
.welcome h2 {
    font-size: 14px;
    text-transform: uppercase;
    letter-spacing: 5px;
    font-weight: 500;
    color: #222;
}
@media (min-width: 320px) and (max-width: 736px) {
.welcome h2 {
        font-size: 12px;
}
}
.section {
  min-height: 100vh;
  display: flex;
  overflow: hidden;
}
@media (min-width: 320px) and (max-width: 736px) {
.section {
      flex-direction: column;
}
}
.section#about .img-container {
    background: #fff;
    height: 100vh;
}
@media (min-width: 320px) and (max-width: 736px) {
.section#about .img-container {
        width: 100vh;
        height: auto;
}
}
.section#about .img-container img {
      object-fit: fill;
      transform: translateX(-50%);
}
.section#home {
    display: flex;
    align-items: flex-end;
    position: relative;
    background: #fff;
}
@media (min-width: 320px) and (max-width: 736px) {
.section#home {
        flex-direction: column;
}
}
.section#home .img-container {
      width: 50vw;
      opacity: 1;
      display: flex;
      align-items: flex-end;
      height: 100vh;
}
@media (min-width: 320px) and (max-width: 736px) {
.section#home .img-container {
          width: 100vh;
          height: auto;
}
}
@media (min-width: 320px) and (max-width: 736px) {
.section#home .content {
        position: absolute;
        top: 0;
        left: 0;
        padding: 0 20px;
}
}
.section#contact {
    overflow: hidden;
}
.section#contact .typeform-widget {
      position: relative;
      left: 15px;
}
.section#contact h1 {
      text-transform: uppercase;
      font-size: 35px;
      font-weight: 600;
}
.section#team {
    background: #fafafa;
}
.section#team h1,
    .section#team h2,
    .section#team p,
    .section#team ul {
      color: #222;
      text-align: left;
}
.section#team ul {
      font-weight: 300;
      font-size: 18px;
      line-height: 1.5;
}
.section#team h2 {
      font-weight: 500;
      font-size: 18px;
}
.section#team h1 {
      font-weight: 500;
      font-size: 35px;
      text-transform: uppercase;
}
.section#team p {
      font-weight: 300;
}
.section#team p strong {
        font-weight: 500;
}
.section#process {
    background: #222;
    height: auto;
    padding: 30px 0;
}
.section#process .content {
      height: auto;
}
.section#process h1 {
      text-transform: uppercase;
      font-size: 35px;
      font-weight: 600;
      margin: 0;
}
.section#services {
    background: #fafafa;
    height: auto;
    padding: 30px 0;
}
.section#services .content {
      height: auto;
}
.section#services h1,
    .section#services h2,
    .section#services p,
    .section#services ul {
      color: #222;
}
.section#services h1 {
      text-transform: uppercase;
      font-size: 35px;
      font-weight: 600;
}
.section#welcome {
    background: #222;
}
.section .content {
    min-height: 100vh;
    display: flex;
    align-items: center;
    justify-content: center;
    flex: 1;
}
.section .img-container {
    min-height: 100vh;
    overflow: hidden;
    flex: 1;
}
.section .img-container img {
      object-fit: contain;
      height: 100%;
}
@media (min-width: 320px) and (max-width: 736px) {
.section .img-container {
        width: 100%;
        height: auto;
        min-height: auto;
}
.section .img-container img {
          width: 100% !important;
          display: block;
}
}
.section p {
    font-size: 20px;
    line-height: 1.5;
    font-weight: 300;
    color: #ebebeb;
    text-align: left;
}
.section .text {
    max-width: 400px;
    width: 100%;
    padding: 0 20px;
}
#app {
  font-family: 'futura-pt', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-align: center;
  margin: 0;
  color: #fafafa;
}

